import { useState, useRef, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "../components/common/Buttons";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Send, Bot, User, X } from "lucide-react";

interface Message { id: string; role: "user" | "assistant"; content: string; timestamp: string }

export function TodoChatbot({ onClose }: { onClose: () => void }) {
  const [messages, setMessages] = useState<Message[]>([
    { id: "1", role: "assistant", content: "Hi! Ask me anything about your todos.", timestamp: new Date().toISOString() }
  ]);
  const [input, setInput] = useState("");
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim()) return;
    const userMessage = { id: Date.now().toString(), role: "user", content: input, timestamp: new Date().toISOString() };
    setMessages(prev => [...prev, userMessage]);

    try {
      const res = await fetch("https://<PROJECT_REF>.functions.supabase.co/todo-chatbot", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query: input })
      });
      const data = await res.json();
      const assistantMessage = { id: (Date.now()+1).toString(), role: "assistant", content: data.answer, timestamp: new Date().toISOString() };
      setMessages(prev => [...prev, assistantMessage]);
    } catch {
      setMessages(prev => [...prev, { id: Date.now().toString(), role: "assistant", content: "Error connecting to chatbot.", timestamp: new Date().toISOString() }]);
    }

    setInput("");
  };

  return (
    <Card className="h-[calc(100vh-8rem)] flex flex-col">
      <CardHeader className="border-b flex justify-between items-center">
        <div>
          <CardTitle className="flex items-center gap-2"><Bot className="h-5 w-5 text-primary" /> Todo Assistant</CardTitle>
          <CardDescription>Ask me anything about your tasks</CardDescription>
        </div>
        <Button variant="ghost" size="icon" onClick={onClose}><X className="h-4 w-4" /></Button>
      </CardHeader>
      <CardContent className="flex-1 flex flex-col p-0">
        <ScrollArea className="flex-1 p-4" ref={scrollRef}>
          <div className="space-y-4">
            {messages.map(msg => (
              <div key={msg.id} className={`flex gap-3 ${msg.role==="user"?"justify-end":""}`}>
                {msg.role==="assistant" && <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center"><Bot className="h-4 w-4 text-primary-foreground"/></div>}
                <div className={`rounded-lg px-4 py-2 max-w-[80%] ${msg.role==="user"?"bg-primary text-primary-foreground":"bg-muted text-foreground border border-border"}`}>
                  <p className="text-sm whitespace-pre-wrap">{msg.content}</p>
                </div>
                {msg.role==="user" && <div className="h-8 w-8 rounded-full bg-accent flex items-center justify-center"><User className="h-4 w-4 text-accent-foreground"/></div>}
              </div>
            ))}
          </div>
        </ScrollArea>
        <div className="border-t p-4 flex gap-2">
          <Input placeholder="Ask about your todos..." value={input} onChange={e=>setInput(e.target.value)} onKeyDown={e=>e.key==="Enter"&&handleSend()}/>
          <Button onClick={handleSend} size="icon"><Send className="h-4 w-4"/></Button>
        </div>
      </CardContent>
    </Card>
  )
}
